.switch {
  height: 20px;
  width: 40px;
  display: inline-block;
  position: relative;
}

.switch__input {
  display: none;
}

.switch__slider {
  cursor: pointer;
  border: .604701px solid #fd5103;
  border-radius: 20px;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1), border-color .3s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switch__slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #fd5103;
  border-radius: 50%;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1), background-color .3s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 2px;
  left: 2px;
}

.switch__input--header:checked + .switch__slider:before, .switch__input:checked + .switch__slider:before {
  background-color: #fff;
  transform: translateX(20px);
}

.switch__input:checked + .switch__slider {
  background-color: #fd5103;
  border-color: #fff;
}

